<template>
    <div>        
    <ABSListVuex
      :prop = "propList"
      :title = "data.QueryName"
      @rowClicked = "$parent.rowClicked"
      @rowDblClicked = "$parent.doDoubleClick"
      @rowLinkClick = "$parent.rowLink"
      @pageSize = "$parent.M_PageSize"
      @pagination = "$parent.M_Pagination"
      @filter = "$parent.M_Advance_Filter"
      @headTable = "$parent.M_Head_Table"
      @refreshColumn = "$parent.refreshColumn"
    />


        <div v-show="$parent.showForm"  :style="'margin-top:10px;'" class="el" mousetip mousetip-msg="I'm a tooltip">
            <div class="div-collapse" v-b-toggle.collapse1>
                <span class="master-span" v-show="IEBy.Input && IEBy.Edit">
                    Input By : {{IEBy.Input}} | Edit By : {{IEBy.Edit}} <font-awesome-icon icon="sort-down" class="icon-style-1__master" /> 
                </span>
            </div>
            <b-collapse id="collapse1" :visible="true">
                <b-row  style="padding-left: 10px; padding-bottom: 10px; !important;">     
					<b-col md="12" id="col-left" class="bColMasterForm">	
						<b-form :data-vv-scope="'FormScope_' + PageLevel + '_' + TabIndex" :data-vv-value-path="'FormScope_' + PageLevel + '_' + TabIndex">
                            <b-row style="padding-left:10px;">					
                                <b-col md="6" id="col-left"> <!-- table open left-->
                                    <!-- <b-row>
                                      <b-col  md="6">
                                        <ABSCompute :prop="PI_depositnovalue" v-model="M_TN_ContractDepositBilling.depositnovalue"  ref="ref_depositnovalue" />
                                      </b-col>
                                    </b-row> -->
                                    <b-row>
                                      <b-col  md="12">
                                        <ABSInputSelectList @change="OntrxtypeChange" :prop="PI_trxtype" :value="M_TN_ContractDepositBilling.trxtype" :label="M_TN_ContractDepositBilling.trxtypeLabel" ref="ref_trxtype"/>
                                      </b-col>
                                    </b-row>
                                    <b-row>
                                      <b-col  md="12">
                                        <ABSTextAreaVuex :prop="PI_descs" v-model="M_TN_ContractDepositBilling.descs"  ref="ref_descs"/>
                                      </b-col>
                                    </b-row>
                                    <b-row>
                                      <b-col  md="12">
                                        <ABSinputDateVuex @input="OnbillingdateChange" :prop="PI_billingdate" v-model="M_TN_ContractDepositBilling.billingdate"  ref="ref_billingdate" />
                                      </b-col>
                                    </b-row>
                                     <b-row>
                                      <b-col  md="12">
                                        <ABSInputSelectList @change="OncredittermscdChange" :prop="PI_credittermscd" :value="M_TN_ContractDepositBilling.credittermscd" :label="M_TN_ContractDepositBilling.credittermscdLabel" ref="ref_credittermscd"/>
                                      </b-col>
                                    </b-row>
                                    <b-row>
                                      <b-col  md="12">
                                        <ABSinputDateVuex @input="OnbillingduedateChange" :prop="PI_billingduedate" v-model="M_TN_ContractDepositBilling.billingduedate"  ref="ref_billingduedate" />
                                      </b-col>
                                    </b-row>
                                    <b-row>
                                      <b-col  md="12">
                                        <ABSInputSelectList @change="OncurrencycdChange" :prop="PI_currencycd" :value="M_TN_ContractDepositBilling.currencycd" :label="M_TN_ContractDepositBilling.currencycdLabel" ref="ref_currencycd"/>
                                      </b-col>
                                    </b-row>
                                    <b-row>
                                      <b-col  md="12">
                                        <ABSinputDateVuex @input="OnrefunddateChange" :prop="PI_refunddate" v-model="M_TN_ContractDepositBilling.refunddate"  ref="ref_refunddate" />
                                      </b-col>
                                    </b-row>
                                </b-col> <!-- table close -->
                                <b-col md="6" id="col-right"> <!-- table open right-->
                                    <b-row>
                                      <b-col  md="12">
                                        <ABSCompute :prop="PI_status" v-model="M_TN_ContractDepositBilling.status"  ref="ref_status" />
                                      </b-col>
                                    </b-row>
                                    <b-row>
                                      <b-col  md="12">
                                        <ABSCompute :prop="PI_depositno" v-model="M_TN_ContractDepositBilling.depositno"  ref="ref_depositno" />
                                      </b-col>
                                    </b-row>
                                    <b-row>
                                      <b-col  md="12">
                                        <ABSInputSelectList @change="OnaddresscdChange" :prop="PI_addresscd" :value="M_TN_ContractDepositBilling.addresscd" :label="M_TN_ContractDepositBilling.addresscdLabel" ref="ref_addresscd"/>
                                      </b-col>
                                    </b-row>
                                    <b-row>
                                      <b-col  md="12">
                                        <ABSinputTextVuex :prop="PI_depositamt" v-model="M_TN_ContractDepositBilling.depositamt"  ref="ref_depositamt"/>
                                      </b-col>
                                    </b-row>
                                    <b-row>
                                      <b-col md="12">
                                        <ABSTextAreaVuex @input="OnremarksChange" :prop="PI_remarks" v-model="M_TN_ContractDepositBilling.remarks"  ref="ref_remarks" />
                                      </b-col>
                                    </b-row>

                                </b-col>
                  
							</b-row>
							<ABSTAnalysis :prop="PTAnalysis" />
						</b-form>
					</b-col>
                </b-row>
            </b-collapse>
        </div>
    </div>
</template>

<script>

export default {
    props: {PageLevel:'', TabIndex: '', data: ''},
    data() {
        return {
			ValKey:null,
            FormType: "List",
			Module:"TN",
            propList: {
                initialWhere:"ApplicationNo = '" + this.ValKey + "'",
                LineNo: this.$parent.data.PageOrder,
                PageLevel: this.PageLevel,
                TabIndex: this.TabIndex, 
                OrderBy: '', 
                SourceField: '', 
                ParamView: '' 
            },
       
            IEBy: {Input: '', Edit: ''},   			

            M_TN_ContractDepositBilling :{
                status: '',
                debtorcd: '',
                trxtype: '',
                descs: '',
                billingdate: '',
                billingduedate: '',
                credittermscd: '',
                currencycd: '',
                addresscd: '',
                depositamt: '0',
                refunddate: '',
                remarks: '',
                trxtypedescs: '',
                addresscontactid: '',
                userinput: '',
                useredit: this.getDataUser().UserId,
                address1: '',
                dueday: 0,
                credittermsdescs: '',
                depositno: '',
                subportfoliocd: this.getDataUser().SubPortfolioCd,
                applicationno: '',
                sequenceno: 0,
                lastupdatestamp: 0,
                rowid: 0
                    }
            ,
            PI_status: { 
                cLabel: 'Status', 
                cLabelSize: 4, 
                cValue: '', 
                cPageLevel: this.PageLevel, 
                cTabIndex: this.TabIndex, 
                cVisible: true 
            }, 
            PI_depositno: { 
                cLabel: 'Deposit No', 
                cLabelSize: 4, 
                cValue: '', 
                cPageLevel: this.PageLevel, 
                cTabIndex: this.TabIndex, 
                cVisible: true 
            }, 
            PI_depositnovalue: { 
                cLabel: 'DepositNoValue', 
                cLabelSize: 4, 
                cValue: '', 
                cPageLevel: this.PageLevel, 
                cTabIndex: this.TabIndex, 
                cVisible: true 
            }, 
            PI_trxtype: { 
                dataLookUp: { 
                    LookUpCd: 'GetLookupTrxTypeTN'    , 
                    ColumnDB: 'TrxType'   , 
                    InitialWhere: "(TenancyChargeType IN ('1', '2', '3', '4', 'Z')) AND ModuleCd = 'AR' and Subportfoliocd = '" + this.getDataUser().SubPortfolioCd + "'",
                    ParamWhere: ''  
                        }, 
                cValidate :'required', 
                cName: 'trxtypeee', 
                cLabel: 'Trans. Type ', 
                cKey: true, 
                cLabelSize: 4, 
                cOrder: 102, 
                cTriggered: false, 
                cDefault: '', 
                cProtect: false, 
                cVisible:  true, 
                cAsync:  false, 
                cFilter: true, 
                cPageLevel: this.PageLevel, 
                cTabIndex: this.TabIndex, 
                cParentForm: 'FormScope_' + this.PageLevel + '_' + this.TabIndex, 
                cStatic: false, 
                cOption: [], 
                cMasterUrl: 'AR/AR_TransactionTypeSubPortfolioMaintenance' ,
                cDisplayColumn: 'TrxType,Descs' ,
            }, 
            PI_descs: { 
                cValidate :'max:150', 
                cName:'desscsss', 
                cLabel:'Description', 
                cLabelSize: 4, 
                cOrder: 103, 
                cKey: false, 
                cDefault: '', 
                cProtect: false, 
                cVisible: true, 
                cResize: false, 
                cReadOnly: false, 
                cRows: 3, 
                cMaxRows: 3, 
                cSize: 'md', 
                cPageLevel: this.PageLevel, 
                cTabIndex: this.TabIndex, 
                cParentForm: 'FormScope_' + this.PageLevel + '_' + this.TabIndex, 
            },  
            PI_billingdate: { 
                cValidate :'required', 
                cName: 'billingdate', 
                cLabel: 'Billing Date ', 
                cLabelSize: 4, 
                cOrder: 104, 
                cKey: false, 
                cVisible:  true, 
                cProtect: false, 
                cPageLevel: this.PageLevel, 
                cTabIndex: this.TabIndex, 
                cParentForm: 'FormScope_' + this.PageLevel + '_' + this.TabIndex 
            }, 
            PI_addresscd: { 
                dataLookUp: { 
                    LookUpCd: 'GetLookupCMContactAndAddressSubportfolio'    , 
                    ColumnDB: 'AddressCd'   , 
                    InitialWhere: ""   , 
                    ParamWhere: ''  
                        }, 
                cValidate :'required', 
                cName: 'addresscd', 
                cLabel: 'Address', 
                cKey: false, 
                cLabelSize: 4, 
                cOrder: 109, 
                cTriggered: false, 
                cDefault: '', 
                cProtect: false, 
                cVisible:  true, 
                cAsync:  false, 
                cFilter: true, 
                cPageLevel: this.PageLevel, 
                cTabIndex: this.TabIndex, 
                cParentForm: 'FormScope_' + this.PageLevel + '_' + this.TabIndex, 
                cStatic: false, 
                cOption: [], 
                cMasterUrl: 'CM/CM_Contact' ,
                cDisplayColumn: 'AddressCd,Address1' ,
            }, 
            PI_currencycd: { 
                dataLookUp: { 
                    LookUpCd: 'GetLookupCMCurrency'    , 
                    ColumnDB: 'CurrencyCd'   , 
                    InitialWhere: ""   , 
                    ParamWhere: ''  
                        }, 
                cValidate :'required', 
                cName: 'currencycd', 
                cLabel: 'Currency ', 
                cKey: false, 
                cLabelSize: 4, 
                cOrder: 107, 
                cTriggered: false, 
                cDefault: '', 
                cProtect: false, 
                cVisible:  true, 
                cAsync:  false, 
                cFilter: true, 
                cPageLevel: this.PageLevel, 
                cTabIndex: this.TabIndex, 
                cParentForm: 'FormScope_' + this.PageLevel + '_' + this.TabIndex, 
                cStatic: false, 
                cOption: [], 
                cMasterUrl: 'CM/CM_CurrencyMaster' ,
                cDisplayColumn: 'CurrencyCd,Descs' ,
            }, 
            PI_depositamt: { 
                cValidate :'required', 
                cName: 'depositamt', 
                cLabel: 'Amount', 
                cLabelSize: 4, 
                cOrder: 110, 
                cKey: false, 
                cType: 'decimal',
                cDecimal: 2, 
                cVisible: true, 
                cProtect: false, 
                cCalculator: true,
                cPageLevel: this.PageLevel, 
                cTabIndex: this.TabIndex, 
                cParentForm: 'FormScope_' + this.PageLevel + '_' + this.TabIndex 
            }, 
            PI_refunddate: { 
                cValidate :'', 
                cName: 'refunddate', 
                cLabel: 'Refund Date', 
                cLabelSize: 4, 
                cOrder: 108, 
                cKey: false, 
                cVisible:  true, 
                cProtect: false, 
                cPageLevel: this.PageLevel, 
                cTabIndex: this.TabIndex, 
                cParentForm: 'FormScope_' + this.PageLevel + '_' + this.TabIndex 
            }, 
            PI_credittermscd: { 
                dataLookUp: { 
                    LookUpCd: 'GetLookupCMCreditTerms'    , 
                    ColumnDB: 'CreditTermsCd'   , 
                    InitialWhere: ""   , 
                    ParamWhere: ''  
                        }, 
                cValidate :'', 
                cName: 'credittermscd', 
                cLabel: 'Credit Terms', 
                cKey: false, 
                cLabelSize: 4, 
                cOrder: 105, 
                cTriggered: false, 
                cDefault: '', 
                cProtect: false, 
                cVisible:  true, 
                cAsync:  false, 
                cFilter: true, 
                cPageLevel: this.PageLevel, 
                cTabIndex: this.TabIndex, 
                cParentForm: 'FormScope_' + this.PageLevel + '_' + this.TabIndex, 
                cStatic: false, 
                cOption: [], 
                cMasterUrl: 'CM/CM_CreditTerms' ,
                cDisplayColumn: 'CreditTermsCd,Descs' ,
            }, 
            PI_billingduedate: { 
                cValidate :'', 
                cName: 'billingduedate', 
                cLabel: 'Billing Due Date', 
                cLabelSize: 4, 
                cOrder: 106, 
                cKey: false, 
                cVisible:  true, 
                cProtect: false, 
                cPageLevel: this.PageLevel, 
                cTabIndex: this.TabIndex, 
                cParentForm: 'FormScope_' + this.PageLevel + '_' + this.TabIndex 
            }, 
            PI_remarks: { 
                cValidate :'', 
                cName:'remarks', 
                cLabel:'Remarks', 
                cLabelSize: 4, 
                cOrder: 111, 
                cKey: false, 
                cDefault: '', 
                cProtect: false, 
                cVisible: true, 
                cResize: false, 
                cReadOnly: false, 
                cRows: 3, 
                cMaxRows: 3, 
                cSize: 'md', 
                cPageLevel: this.PageLevel, 
                cTabIndex: this.TabIndex, 
                cParentForm: 'FormScope_' + this.PageLevel + '_' + this.TabIndex, 
            }, 

			PTAnalysis: {
                cPageLevel: this.PageLevel,
                cTabIndex: this.TabIndex,
                cPageMasterSeq: this.data.PageMasterSeq,
                cFormPageNo: this.data.PageOrder
            },
            //ContractData: []   

        }
    },
	computed : {
    inputStatus() {
      if(this.$store.getters.getLevel == this.PageLevel && this.$store.getters.getTab == this.TabIndex){
        return this.$store.getters.getStatus.toUpperCase()
      }
      else {
        return 'VIEW'
      }
    },
	DataRow(){
		return this.$store.getters.GetPage2Data
	},
    DataRowPage1(){
		return this.$store.getters.GetPage1Data
    },
    GetDataByPage1() {
        return this.$store.getters.GetPage1GetDataBy
    }
  },
    methods: {
        // getContractData (appNo) {
        //   var param = {
        //     OptionFunctionCd: "GetContractData",
        //     ModuleCd: "TN",
        //     SubPortfolioCd: this.getDataUser().SubPortfolioCd,
        //     ApplicationNo: appNo
        //   }

        //   this.FnDynamicFunction(param)
        //   .then(ress => {
        //     if (ress == null) return
        //     if (ress.length < 1) {
        //       return
        //     }
        //     this.ContractData = ress[0]
        //   })
        // },
        GetDefaultDepositBilling(){
            var ttlarea = 0
            switch (this.GetDataByPage1.chargearea) {
                case "N":
                    ttlarea = parseFloat(this.replaceAllString(this.GetDataByPage1.totalnetarea,',','','number')) //this.isCurrency(this.ContractData.TotalNetArea, this.decimal)
                    break;
                case "S":
                    ttlarea = parseFloat(this.replaceAllString(this.GetDataByPage1.totalsemigrossarea,',','','number')) //this.isCurrency(this.ContractData.TotalSemiGrossArea, this.decimal)
                    break;
                default:
                    ttlarea = parseFloat(this.replaceAllString(this.GetDataByPage1.totalgrossarea,',','','number')) //this.isCurrency(this.ContractData.TotalGrossArea, this.decimal)
                    break;
            }
             var param = {
                    OptionFunctionCd: "GetTransactionTypeMapping",
                    ModuleCd: this.Module,
                    SubPortfolioCd: this.getDataUser().SubPortfolioCd,
                    TrxType:this.M_TN_ContractDepositBilling.trxtype
                  }

                this.FnDynamicFunction(param)
                .then(ress => {
                  if (ress == null|| ress.length==0) {
                    return
                    }
                var data = ress[0]
                var BrenRate = this.GetDataByPage1.baserentalrate == '' || this.GetDataByPage1.baserentalrate == null ? 0 : parseFloat(this.replaceAllString(this.GetDataByPage1.baserentalrate,',','','number'))
                var SdepRent = this.GetDataByPage1.securitydepositrental == '' || this.GetDataByPage1.securitydepositrental == null ? 0 : this.GetDataByPage1.securitydepositrental
                var SC = this.GetDataByPage1.servicecharge == '' || this.GetDataByPage1.servicecharge == null ? 0 : parseFloat(this.replaceAllString(this.GetDataByPage1.servicecharge,',','','number'))
                var SDS = this.GetDataByPage1.securitydepositsc == '' || this.GetDataByPage1.securitydepositsc == null ? 0 : this.GetDataByPage1.securitydepositsc
                var TLine = this.GetDataByPage1.telephoneline == '' || this.GetDataByPage1.telephoneline == null ? 0 : this.GetDataByPage1.telephoneline
                var TDeposit = this.GetDataByPage1.telephonedeposit == '' || this.GetDataByPage1.telephonedeposit == null ? 0 : parseFloat(this.replaceAllString(this.GetDataByPage1.telephonedeposit,',','','number'))
                var FODeposit = this.GetDataByPage1.fitoutdeposit == '' || this.GetDataByPage1.fitoutdeposit == null ? 0 : parseFloat(this.replaceAllString(this.GetDataByPage1.fitoutdeposit,',','','number'))

                switch (data.TenancyChargeType) {
                    case "1":
                        this.M_TN_ContractDepositBilling.depositamt = this.isCurrency( ttlarea * BrenRate * SdepRent,this.decimal)
                        break;
                    case "2":
                        this.M_TN_ContractDepositBilling.depositamt = this.isCurrency( SC * ttlarea * SDS,this.decimal)
                        break;
                    case "3":
                        this.M_TN_ContractDepositBilling.depositamt = this.isCurrency( TLine * TDeposit,this.decimal)
                        break;
                    case "4":
                        this.M_TN_ContractDepositBilling.depositamt = this.isCurrency( ttlarea * FODeposit,this.decimal)
                        break;                    
                    default:
                        this.M_TN_ContractDepositBilling.depositamt = "0"
                        break;
                }

            })

                
        },
        GetDefault(){
             var param = {
                  OptionFunctionCd: "GetDefaultContractDepositBilling",
                  ModuleCd: this.Module,
                  SubPortfolioCd: this.getDataUser().SubPortfolioCd,
                  ApplicationNo: this.DataRowPage1.ApplicationNo,
                  TrxType:'NULL'
                }
                this.FnDynamicFunction(param)
                .then(ress => {
                    if(ress ==null) return
                    var data = ress[0]
                    this.M_TN_ContractDepositBilling.addresscd = data.AddressCd
                    this.M_TN_ContractDepositBilling.addresscdLabel = data.AddressCd+'|'+data.Address1

                    this.M_TN_ContractDepositBilling.depositamt = this.isCurrency(data.Amount, this.decimal)
                })

        },
        GetCreditTerms(){
            if(this.M_TN_ContractDepositBilling.trxtype !=''){
                var param = {
                  OptionFunctionCd: "GetCreditTermCdByTrxType",
                  ModuleCdV2: this.Module,
                  SubPortfolioCd: this.getDataUser().SubPortfolioCd,
                  ModuleCd:'AR',
                  TrxType: this.M_TN_ContractDepositBilling.trxtype
                }
                this.FnDynamicFunction(param)
                .then(ress => {
                    if(ress ==null) return
                    var data = ress[0]
                    this.M_TN_ContractDepositBilling.credittermscd = data.CreditTermsCd
                    this.M_TN_ContractDepositBilling.credittermscdLabel = data.CreditTermsCd+'|'+data.Descs

                    if(this.M_TN_ContractDepositBilling.credittermscd != ''&& this.M_TN_ContractDepositBilling.credittermscd != null){
                        this.DisplayCreditTerms(data.CreditTermsCd)
                    }
                })
            }
        },
        DisplayCreditTerms(Svalue){
            if(Svalue != ''){
                var param = {
                  OptionFunctionCd: "GetDataCreditTerm",
                  ModuleCd: this.Module,
                  CreditTermsCd: Svalue
                }
                this.FnDynamicFunction(param)
                .then(ress => {
                    if(ress ==null) return
                    var data = ress[0]
                    this.M_TN_ContractDepositBilling.dueday = data.DueDay
                    // console.log('DisplayCreditTerms ',this.M_TN_ContractDepositBilling.dueday'+data.Dueday.toString()+' billing date : '+this.M_TN_ContractDepositBilling.billingdate)
                    // console.log('DisplayCreditTerms '+data.DueDay+' billing date : '+this.M_TN_ContractDepositBilling.billingdate)
                    if (data.DueDay != null && data.DueDay != '') {
                      if (this.M_TN_ContractDepositBilling.billingdate && this.M_TN_ContractDepositBilling.billingdate != '') {
                        //   console.log('date add')
                        this.M_TN_ContractDepositBilling.billingduedate = this.dateAdd2( 'd', this.M_TN_ContractDepositBilling.billingdate, parseInt(data.DueDay) )
                      }
                    }
                })
            }

        },
        OntrxtypeChange (data) {
            //console.log(data);
        this.$nextTick(() => {
            this.M_TN_ContractDepositBilling.trxtype = data.id
            this.M_TN_ContractDepositBilling.trxtypeLabel = data.label
            if (this.inputStatus != "VIEW") {
                // alert(JSON.stringify(data,null,0))
                this.M_TN_ContractDepositBilling.descs = data.Descs
                this.FnGetTrxTypeData(this.getDataUser().SubPortfolioCd,'AR', data.id)
                .then(dt => {
                    if (dt.length == 0) return
                    var data = dt[0]
                    this.M_TN_ContractDepositBilling.descs = data.TrxTypeDescs
                    this.M_TN_ContractDepositBilling.currencycd = data.CurrencyCd
                    this.M_TN_ContractDepositBilling.currencycdLabel = data.CurrencyCd

                    this.GetCreditTerms()
                    this.GetDefaultDepositBilling()
                })
            }
        })
        this.$forceUpdate()
        },
        OnbillingdateChange (data) {
        this.$nextTick(() => {
            if (this.inputStatus != "VIEW") {
                if (this.M_TN_ContractDepositBilling.credittermscd && this.M_TN_ContractDepositBilling.credittermscd != '') {
                    this.DisplayCreditTerms(this.M_TN_ContractDepositBilling.credittermscd)
                }
                else {
                    this.M_TN_ContractDepositBilling.billingduedate = data
                }
            }
        })
        this.$forceUpdate()
        },
        OnaddresscdChange (data) {
        this.$nextTick(() => {
            this.M_TN_ContractDepositBilling.addresscd = data.id
            this.M_TN_ContractDepositBilling.addresscdLabel = data.label
            if (this.inputStatus != "VIEW") {
//{addresscd}
            }
        })
        this.$forceUpdate()
        },
        OncurrencycdChange (data) {
        this.$nextTick(() => {
            this.M_TN_ContractDepositBilling.currencycd = data.id
            this.M_TN_ContractDepositBilling.currencycdLabel = data.label
            if (this.inputStatus != "VIEW") {
//{currencycd}
            }
        })
        this.$forceUpdate()
        },
        OnrefunddateChange (data) {
        this.$nextTick(() => {
            if (this.inputStatus != "VIEW") {
//{refunddate}
            }
        })
        this.$forceUpdate()
        },
        OncredittermscdChange (data) {
        this.$nextTick(() => {
            this.M_TN_ContractDepositBilling.credittermscd = data.id
            this.M_TN_ContractDepositBilling.credittermscdLabel = data.label
            if (this.inputStatus != "VIEW") {
                this.DisplayCreditTerms(data.id)
            }
        })
        this.$forceUpdate()
        },
        OnbillingduedateChange (data) {
        this.$nextTick(() => {
            if (this.inputStatus != "VIEW") {
//{billingduedate}
            }
        })
        this.$forceUpdate()
        },
