                if (
                    (data.StatusCd != 'A' && data.ContractStatus != 1) ||
                    (data.StatusCd != 'N' && data.ContractStatus != 1) ||
                    (data.StatusCd == 'C') ||
                    (data.StatusCd == 'X')
                ) {
                    this.FormType = "View"
                    this.$store.commit('setFormType','View')
                    this.getToolbar().ProcessPS()
                }
                else {
                    this.FormType = "List"
                    this.$store.commit('setFormType','List')
